:root {
  --color-prefilled: white;
  --color-solved: white;
  --color-draft: white;
  --color-highlight-fg: white;
  --color-highlight-bg1: white;
  --color-highlight-bg2: white;
  --color-bg: white;
  --default-color-prefilled: #050505;
  --default-color-solved: #156363;
  --default-color-draft: #447862;
  --default-color-highlight-fg: #007896;
  --default-color-highlight-bg1: #dcc1c3;
  --default-color-highlight-bg2: #dcd1d180;
  --default-color-bg: #f7f3e8;
  --scale-factor: 1;
}

body {
  padding-top: env(safe-area-inset-top);
  padding-bottom: env(safe-area-inset-bottom);
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
  user-select: none;
  background-color: var(--color-bg);
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  font-family: sans-serif;
  display: flex;
  overflow: hidden;
}

#app-container {
  box-sizing: border-box;
  flex: 1 0 auto;
  width: 640px;
  margin: 0 auto;
  position: relative;
}

@media (width <= 640px) {
  #app-container {
    max-width: 100%;
  }
}

#app-container > div.page {
  opacity: 0;
  z-index: -100;
}

#app-container > div.page.visible {
  opacity: 1;
  z-index: 0;
}

.hidden {
  z-index: -200 !important;
  opacity: 0 !important;
}

#init-page {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
  position: absolute;
}

#init-page > div {
  text-align: center;
  width: 30%;
  margin: 40px;
  padding: 20px;
  font-size: 2em;
}

#loading-page {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  font-size: 3em;
  display: flex;
  position: absolute;
}

.btn-default {
  user-select: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 8px;
  transition: background-color .2s, color .2s, border-color .2s;
}

.btn-default.highlight {
  color: var(--color-highlight-fg);
  border-color: var(--color-highlight-fg);
}

.btn-default.enabled {
  cursor: pointer;
}

.btn-default.disabled {
  opacity: .3;
}

.btn-default.enabled:hover {
  background-color: #f2eae0;
}

.btn-default.enabled:active {
  background-color: #d0d0d0;
}

#game-page {
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  display: flex;
  position: relative;
}

#game-page > div {
  box-sizing: border-box;
}

#game-page #notification {
  text-align: center;
  width: 100%;
  color: var(--color-draft);
  transition: all .7s ease-in-out;
  position: absolute;
}

#game-page #board-banner {
  justify-content: space-between;
  width: 100%;
  padding: 0 16px;
  display: flex;
  position: relative;
}

#game-page #board-banner > span {
  display: inline-block;
}

#game-page #board {
  margin: -8px 0;
}

#game-page #num-keyboard {
  width: calc(200px * var(--scale-factor));
  z-index: 100;
  background: var(--color-bg);
  border-radius: calc(20px * var(--scale-factor));
  border: darkgray calc(2px * var(--scale-factor)) solid;
  padding: calc(12px * var(--scale-factor));
  position: absolute;
}

#game-page #num-keyboard #draft-mode-line {
  vertical-align: middle;
  height: calc(26px * var(--scale-factor));
  line-height: calc(26px * var(--scale-factor));
  margin-bottom: calc(12px * var(--scale-factor));
  font-size: calc(1em * var(--scale-factor));
}

#game-page #num-keyboard .keyboard {
  gap: calc(10px * var(--scale-factor));
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  margin: 0 auto;
  display: grid;
}

#game-page #num-keyboard .key {
  font-family: monospace;
  font-size: calc(36px * var(--scale-factor));
  justify-content: center;
  align-items: center;
  display: flex;
}

#game-page #num-keyboard .switch-container {
  align-items: center;
  margin-top: 20px;
  display: flex;
}

#game-page #num-keyboard .switch {
  width: calc(48px * var(--scale-factor));
  height: calc(26px * var(--scale-factor));
  display: inline-block;
  position: relative;
}

#game-page #num-keyboard .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

#game-page #num-keyboard .slider {
  cursor: pointer;
  border-radius: calc(26px * var(--scale-factor));
  height: calc(26px * var(--scale-factor));
  background-color: #ccc;
  transition: all .1s;
  position: absolute;
  inset: 0;
}

#game-page #num-keyboard .slider:before {
  content: "";
  height: calc(18px * var(--scale-factor));
  width: calc(18px * var(--scale-factor));
  left: calc(4px * var(--scale-factor));
  bottom: calc(4px * var(--scale-factor));
  background-color: #fff;
  border-radius: 50%;
  transition: all .1s;
  position: absolute;
}

#game-page #num-keyboard input:checked + .slider {
  background-color: var(--color-highlight-fg);
}

#game-page #num-keyboard input:focus + .slider {
  box-shadow: 0 0 1px var(--color-highlight-fg);
}

#game-page #num-keyboard input:checked + .slider:before {
  -webkit-transform: translateX(calc(22px * var(--scale-factor)));
  -ms-transform: translateX(calc(22px * var(--scale-factor)));
  transform: translateX(calc(22px * var(--scale-factor)));
}

#game-page #num-keyboard .switch-label {
  margin-right: 10px;
}

#game-page #board-buttons {
  text-align: center;
  flex-flow: wrap;
  justify-content: space-around;
  row-gap: 30px;
  margin: 4px;
  padding: 24px 0;
  font-size: 1.2em;
  display: flex;
}

#game-page #board-buttons > span {
  width: 18%;
  min-width: 120px;
  display: inline-block;
}

@media (width <= 600px) {
  #game-page #board-buttons > span {
    flex-basis: 34%;
  }
}

.fading {
  transition: opacity .5s ease-in-out;
}

.fading-fast {
  transition: all 80ms ease-in-out;
}

#build-info {
  text-align: right;
  color: #d3d3d3;
  padding-right: 10px;
  font-size: 10px;
  position: fixed;
  bottom: 0;
  right: 0;
}
